import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NgbTabset } from '@ng-bootstrap/ng-bootstrap';
import { Title } from '@angular/platform-browser';

import { first } from 'rxjs/operators';

import { DeductionsEnabledService } from '@app/deductions/deductions-enabled.service';
import { Employee } from '@model';
import { EmployeeViewService } from '@employees/employee/employee-view.service';
import { getFullName, hasName } from '@utils';
import { tabRouteAnim } from '@animations';
import { SubSink, WellKnownRoutes } from '@core';

interface TabInfo {
  id: string;
  title: string;
  link: string;
}

const commonTabs = {
  personal: { id: 'personal', title: 'Personal', link: 'personal' } as TabInfo,
  employment: { id: 'employment', title: 'Employment', link: 'employment' } as TabInfo,
  payment: { id: 'payment', title: 'Payment', link: 'payment' } as TabInfo,
  deductionsTab: { id: 'deductions', title: 'Deductions', link: 'deductions'}  as TabInfo,
  taxTab: { id: 'tax', title: 'Taxes', link: 'tax' } as TabInfo
};

@Component({
  selector: 'sp-ee-container',
  templateUrl: './employee-container.component.html',
  animations: [tabRouteAnim]
})
export class EmployeeContainerComponent implements OnInit, OnDestroy {

  @ViewChild('tabset')
  tabset: NgbTabset;

  backLink: string = WellKnownRoutes.Employees;
  nextLink = commonTabs.employment.link;
  nextLabel = 'Continue';

  currentTabTitle = commonTabs.personal.title;
  initialId: string; // initial tab id; tabsetSelect changes thereafter
  isNew = false;
  private sub = new SubSink();
  vm: Employee;

  tabConfigurations: TabInfo[];

  private tabLink: string; // link to the tab currently (or about to be) displayed

  constructor(
    private deductionsEnabledService: DeductionsEnabledService,
    private employeeViewService: EmployeeViewService,
    private ngTitle: Title,
    private route: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.setTabs();
    this.watchRouteChange();
    this.watchVmChange();
  }

  ngOnDestroy(): void {
    this.sub.unsubscribe();
  }

  get disabled() {
    return !this.hasName && !this.isNew;
  }

  get disableNext() {
    return !this.hasName;
  }

  get hasName() {
    return hasName(this.vm);
  }

  private setTabs() {
    this.deductionsEnabledService.showDeductions$.pipe(first()).subscribe(showDeductions => {
      this.tabConfigurations = [
        commonTabs.personal,
        commonTabs.employment,
        commonTabs.payment,
        commonTabs.deductionsTab,
        commonTabs.taxTab
      ]
      .filter(tab => showDeductions || tab.id !== commonTabs.deductionsTab.id);
    });
  }

  /** Listen for route changes. Many of them re-use this component **/
  private watchRouteChange() {
    this.sub.sink = this.route.url.subscribe(url => {
      const eeId = url.toString();
      this.employeeViewService.setCurrentEmployeeId(eeId);
      window.scrollTo(0, 0);
      this.tabLink = this.route.snapshot.firstChild.url.toString() || this.tabConfigurations[0].link;
      this.initialId = this.initialId || this.tabLink; // only useful first time
      // Wait a tick for tabs to draw
      setTimeout(() => this.updateCurrentTabInfo(this.tabLink));
    });
  }

  /** Listen for ViewModel change,  typically from setting a new EE Id or saving */
  private watchVmChange() {
    this.sub.sink = this.employeeViewService.currentEmployee$.subscribe(ee => {
      this.vm = ee;
      this.isNew = !hasName(ee); // is new if the employee-in-cache lacks a name
      this.setBrowserTabTitle();
    });
  }

  private setBrowserTabTitle() {
    this.ngTitle.setTitle(`SP EE ${getFullName(this.vm)} ${this.currentTabTitle}`.trim());
  }

  /**
   * When the current tab changes, setup the tab-related info
   * such as the tab title, the browser tab title, the "next" link,
   */
  private updateCurrentTabInfo(tabLink: string) {
    if (this.tabset) {
      this.tabset.select(tabLink);
    }
    let tabIx = this.tabConfigurations.findIndex(tt => tt.link === tabLink);
    tabIx = tabIx < 0 ? 0 : tabIx;
    this.currentTabTitle = this.tabConfigurations[tabIx].title || '';
    const backTab = this.tabConfigurations[tabIx - 1];
    const nextTab = this.tabConfigurations[(tabIx += 1)];
    // On overflow, navigates away to the EmployeeList!
    this.backLink = backTab ? backTab.link : WellKnownRoutes.Employees;
    this.nextLink = nextTab ? nextTab.link : WellKnownRoutes.Employees;
    this.setBrowserTabTitle();
  }
}
