import { Component } from '@angular/core';
import { RouterOutlet } from '@angular/router';
import { eeSetupAnim } from '@animations';

@Component({
  selector: 'sp-ees-container',
  template: `
    <div [@eeSetupAnim]="prepareRoute(outlet)" class="d-flex flex-column" id="employeesComponent">
      <router-outlet #outlet="outlet"></router-outlet>
    </div>
  `,
  animations: [eeSetupAnim]
})
export class EmployeesContainerComponent {
  prepareRoute(outlet: RouterOutlet) {
    /** Animation relies on title data property to recognize a state change */
    return (
      (outlet && outlet.activatedRouteData && outlet.activatedRouteData['animation']) || 'Employee'
    );
  }
}
