import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { NgbTabset } from '@ng-bootstrap/ng-bootstrap';

import { combineLatest, Observable } from 'rxjs';
import { first, map, take } from 'rxjs/operators';

import { DeductionsEnabledService } from '@app/deductions/deductions-enabled.service';
import { EarningsEnabledService } from '@app/earnings/earnings-enabled.service';
import { tabRouteAnim } from '@animations';
import { WellKnownRoutes } from '@core';
import { SessionService } from '@app/store/session';

interface TabConfiguration {
  link: string; title: string;
}
const allTabConfigurations = {
  employees: { link: './', title: 'Employees' } as TabConfiguration,
  deductions: { link: './company-deductions', title: 'Deductions' } as TabConfiguration,
  earnings: { link: './earnings', title: 'Earnings' } as TabConfiguration,
};

export class EmployeeSetupVm {
  showTabs = false;
}

@Component({
  selector: 'sp-employee-setup-container',
  templateUrl: './employee-setup-container.component.html',
  animations: [tabRouteAnim]
})
export class EmployeeSetupContainerComponent implements OnInit {
  @ViewChild('tabset')
  tabset: NgbTabset;

  backLink: string = WellKnownRoutes.CompanyInfo;
  nextLink: string = WellKnownRoutes.CompanyTaxInfo;

  private currentTab: TabConfiguration;
  initialId: string; // initial tab id; tabsetSelect changes thereafter
  tabConfigurations: TabConfiguration[] = [allTabConfigurations.employees];
  vm$: Observable<EmployeeSetupVm>;

  constructor(
    private deductionsEnabledService: DeductionsEnabledService,
    private earningsEnabledService: EarningsEnabledService,
    private ngTitle: Title,
    private route: ActivatedRoute,
    private sessionService: SessionService
  ) {
    this.currentTab = this.tabConfigurations[0];
  }

  ngOnInit(): void {
    this.vm$ = combineLatest(
        this.deductionsEnabledService.showDeductions$,
        this.earningsEnabledService.showEarnings$,
      ).pipe(
        map(([showDeductions, showEarnings]) => {
          this.tabConfigurations = [allTabConfigurations.employees];
          if (showEarnings) {
            this.tabConfigurations.push( allTabConfigurations.earnings );
          }
          if (showDeductions) {
            this.tabConfigurations.push(allTabConfigurations.deductions);
        }
          let forceHideTabs = false;
          this.sessionService.currentEmployeeId$.pipe(take(1)).subscribe(val => { forceHideTabs = (val != null); });

          const vm: EmployeeSetupVm = {
            showTabs: (showDeductions || showEarnings) && !forceHideTabs
          };
          return vm;
        })
      );

    this.watchRouteChange();
  }

  private getUrlPath(): string {
    if (this.route.snapshot.firstChild) {
      return './' + this.route.snapshot.firstChild.url.join('/');
    }
    return '';
  }

  private setBrowserTabTitle() {
    this.ngTitle.setTitle('SP ' + this.currentTab.title);
  }

  /** Listen for route changes. Many of them re-use this component **/
  private watchRouteChange() {
    this.route.url.subscribe(url => { // THE URL VALUE ISN'T ACTUALLY USED. REFACTOR
      window.scrollTo(0, 0);
      const link = this.getUrlPath() || this.tabConfigurations[0].link;
      this.initialId = this.initialId || link; // only useful first time
      // Wait a tick for tabs to draw
      setTimeout(() => this.updateCurrentTabInfo(link));
    });
  }

  /**
   * When the current tab changes, setup the tab-related info
   * such as the tab title, the browser tab title, the "next" link,
   */
  private updateCurrentTabInfo(tabLink: string) {
    this.currentTab = this.tabConfigurations.find(t => t.link === tabLink) ||
      this.tabConfigurations[0];
    if (this.tabset) {
      this.tabset.select(tabLink); // constraint: select expects an ID and not a link -> forcing id == link
    }
    this.setBrowserTabTitle();
  }

}
