import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { Observable } from 'rxjs';

import { EmployeeContainerComponent } from './employee/employee-container/employee-container.component';
import { EmployeesContainerComponent } from './employees-container.component';
import { EmployeeEmploymentComponent } from './employee/employee-employment/employee-employment.component';
import { EmployeeListContainerComponent } from './employee-setup/employee-list/employee-list-container.component';
import { EmployeePaymentComponent } from './employee/employee-payment/employee-payment/employee-payment.component';
import { EmployeePersonalComponent } from './employee/employee-personal/employee-personal.component';
import { EmployeeTaxesComponent } from './employee/employee-tax/employee-taxes.component';

import { PageLeaveGuard, DataEntryPageGuard } from '@core/routing-guards';
import { EmployeeSetupContainerComponent } from './employee-setup/employee-setup-container/employee-setup-container.component';

const routes: Routes = [
  {
    path: '',
    component: EmployeesContainerComponent,
    canActivateChild: [DataEntryPageGuard],
    canDeactivate: [PageLeaveGuard],
    children: [
      {
        path: '',
        component: EmployeeSetupContainerComponent,
        // canDeactivate: [PageLeaveGuard], // this is a read-only component
        data: { animation: 'EmployeeSetup' },
        children: [
          {
            path: '',
            component: EmployeeListContainerComponent,
            canDeactivate: [PageLeaveGuard],
            data: { title: 'Employees' },
          },
          {
            path: 'earnings',
            loadChildren: '@app/earnings/earnings.module#EarningsModule',
          },
          {
            path: 'company-deductions',
            loadChildren: '@app/deductions/company/company-deductions.module#CompanyDeductionsModule',
          },
        ]
      },
      {
        path: ':id',
        component: EmployeeContainerComponent,
        children: [
          { path: '', redirectTo: 'personal' },
          {
            path: 'personal',
            component: EmployeePersonalComponent,
            canDeactivate: [PageLeaveGuard],
          },
          {
            path: 'employment',
            component: EmployeeEmploymentComponent,
            canDeactivate: [PageLeaveGuard],
          },
          {
            path: 'payment',
            component: EmployeePaymentComponent,
            canDeactivate: [PageLeaveGuard],
          },
          {
            path: 'tax',
            component: EmployeeTaxesComponent,
            canDeactivate: [PageLeaveGuard],
          },
          {
            path: 'deductions',
            loadChildren: '@app/deductions/employee/employee-deductions.module#EmployeeDeductionsModule',
          },
          { path: '**', redirectTo: 'personal' } // bad routes redirect to personal
        ]
      },
      { path: '**', pathMatch: 'prefix', redirectTo: '' }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  providers: [ PageLeaveGuard ]
})
export class EmployeesRoutingModule {}
