import { Injectable } from '@angular/core';

import { combineLatest, Observable, of } from 'rxjs';
import { concatMap, debounceTime, first, map, shareReplay, tap, withLatestFrom } from 'rxjs/operators';

import { addressToString, areDifferent, getFullName } from '@utils';
import { CompanyService, EmployeeService, ReviewService, getEmployeeId } from '@entity';
import { Address, Company, Employee, EmployeePaymentMethod } from '@model';
import { EmployeeListVm, EmployeeVm } from './employee-list-vm';
import { BusyService, Saver } from '@core';
import { SessionService } from '@session';

@Injectable({providedIn: 'root'})
export class EmployeeListViewService {

  /**
   * Live observable of Employee VMs to display in the EmployeeListComponent.
   * Changes infrequently during the life of the component.
   * Must unsubscribe.
   */
  employeeVms$: Observable<EmployeeVm[]>;

  /**
   * Live observable of EmployeeListComponent's VM.
   * Changes frequently
   * Must unsubscribe.
   */
  vm$: Observable<EmployeeListVm>;

  constructor(
    private busyService: BusyService,
    private companyService: CompanyService,
    private employeeService: EmployeeService,
    reviewService: ReviewService,
    private sessionService: SessionService,
    ) {

    // EE ids for employees with a validation error
    const eeErrors$ = reviewService.employeesErrors$.pipe(
      withLatestFrom(employeeService.entities$),
      map(([ves, employees]) => {
        return ves.reduce((acc, ve) => {
          const model = ve.context.model;
          const eeId = getEmployeeId(ve.context, employees);
          if (eeId) {
            acc[eeId] = true;
          }
          return acc;
        }, {} as {[eeId: string]: boolean});
      }),
    );

    this.employeeVms$ = combineLatest(
        employeeService.entities$,
        eeErrors$
      ).pipe(
        debounceTime(0), // let all inputs settle synchronously
        map(([ees, eeErrors]) => {
          return ees.map(ee => {
            return {
              id: ee.id,
              name: getFullName(ee) || 'Unnamed',
              type: ee.isTerminated ? 'terminated' : ee.employeeType || '',
              badgeStatus: ee.isRemoved ? 'Deleted' : eeErrors[ee.id] ? 'In Progress' : 'Complete',
              isRemoved: ee.isRemoved === true
            } as EmployeeVm;
          });
        })
    );

    this.vm$ = combineLatest(
      companyService.currentCompany$,
      sessionService.isAdmin$,
      sessionService.readOnly$
    ).pipe(
      debounceTime(0), // let all inputs settle synchronously
      map(([company, isAdmin, readOnly]) => {
        const co: Company = {
          ...company,
          // Force a true/false value. The default is false: not done
          doneAddingEmployees: company.doneAddingEmployees === true
        };

        // Used to `showRemoved` if user is Admin.
        // DISABLING until we're sure "unremove" is working. PR 516
        // const howRemoved$ = isAdmin;
        const showRemoved = false;

        const vm: EmployeeListVm = {
          company: co,
          readOnly,
          showRemoved
        };

        return vm;
      })
    );
  }

  createNewEmployee(): Observable<Employee> {
    const result$ = this.sessionService.currentCompanyId$.pipe(
      first(),
      concatMap(currentCompanyId => {
        const ee = new Employee();
        ee.id = this.sessionService.generateId();
        ee.companyId = currentCompanyId;

        // Create the new Home Address to go with the new EE
        const home = Address.createNewEmployeeAddress(ee);
        home.id = this.sessionService.generateId();
        home.companyId = currentCompanyId;
        ee.homeAddressId = home.id;

        // Create the new EmployeePaymentMethod to go with the new EE
        const epay = new EmployeePaymentMethod();
        epay.id = this.sessionService.generateId();
        epay.companyId = currentCompanyId;
        epay.employeeId = ee.id;

        return this.employeeService.saveNewEmployee(ee, home, epay);
      })
    );

    return this.busyService.busy$('Saving new employee', result$);
  }

  saveCompany(companyVm: Company): Observable<Company> {
    return this.companyService.currentCompany$.pipe(
      first(),
      concatMap(cachedCompany => {
        const chgCompany: Company = {
          ...cachedCompany,
          // only consider the properties that could change in this view
          doneAddingEmployees: companyVm.doneAddingEmployees === true, // force a value, default to false
          needDeductionSetup: companyVm.needDeductionSetup,
          hasPriorPayroll: companyVm.hasPriorPayroll
        };
        if ( (areDifferent(chgCompany, cachedCompany, 'Company')) ) {
          return this.companyService.update(chgCompany);
        } else {
          return Saver.NO_SAVE; // nothing to do
        }
      })
    );
  }

  saveEmployeeIsRemoved(eeVm: { id: string, isRemoved: boolean }): Observable<Employee> {
    return this.employeeService.saveEmployeeIsRemovedById(eeVm.id, eeVm.isRemoved);
  }
}
