import { Component, Input, OnInit, Optional, ViewChild } from '@angular/core';

import { of, Observable } from 'rxjs';
import { tap } from 'rxjs/operators';

import { BankRoutingNumberService, BankRoutingResult, Saver } from '@app/core';
import { EmployeePaymentAccountVm } from '../employee-payment-account-vm';
import { EmployeePaymentMethod, PaymentAccountType, PaymentMethod } from '@model';
import { EmployeePaymentMethodVm, ActiveAccounts } from '../employee-payment-method-vm';
import { EmployeePaymentAccountViewService } from './employee-payment-account-view.service';
import { ExitEvent, ModelComponent } from '@app/shared';
import { ngIfAnim } from '@animations';

/** Instance of a "Do nothing" version for when real BankRoutingNumberService is unavailable */
export const noopBankRoutingNumberService: BankRoutingNumberService = {
  getBankName: (rn: string) => of({ message: '', rn, name: '', code: 0 } as BankRoutingResult)
} as any;

@Component({
  selector: 'sp-ee-payment-accounts',
  templateUrl: './employee-payment-accounts.component.html',
  animations: [ngIfAnim]
})
export class EmployeePaymentAccountsComponent implements OnInit {
  @Input() eeId: string;
  @ViewChild(ModelComponent) modelComponent: ModelComponent;

  accountTypes2: PaymentAccountType[] = [
    PaymentAccountType.Checking,
    PaymentAccountType.Savings,
    // no PrintedCheck
  ];

  accountTypes3: PaymentAccountType[] = [
    ...this.accountTypes2,
    PaymentAccountType.PrintedCheck
  ];

  accountTypes = this.accountTypes3;

  bankNameVm: string;

  // Enums exposed in the template
  PaymentMethod = PaymentMethod;
  PaymentAccountCount = ActiveAccounts;
  PaymentAccountType = PaymentAccountType;

  public vm: EmployeePaymentMethodVm;
  vm$: Observable<EmployeePaymentMethodVm>;

  constructor(
    private viewService: EmployeePaymentAccountViewService,
    @Optional() private bankRnService?: BankRoutingNumberService
  ) {
    if (!this.bankRnService) {
      // no op version
      this.bankRnService = noopBankRoutingNumberService;
    }
  }

  ngOnInit() {
    this.vm$ = this.viewService.getEmployeePaymentMethodVm$(this.eeId).pipe(
      tap(vm => {
        this.vm = vm;
        this.setPaymentMethod();
      })
    );
  }

  get accounts(): EmployeePaymentAccountVm[] {
    return this.vm.accounts;
  }

  accountTypeChanged(
    account: EmployeePaymentAccountVm,
    newType: PaymentAccountType
  ) {
    account.accountType = newType;

    if (newType !== PaymentAccountType.PrintedCheck && !account.bankNameVm) {
      this.getBankName(account);
    }

    if (newType === PaymentAccountType.PrintedCheck) {
      // Only the current account can be a printedCheck: set others account to null if PrintedCheck
      this.vm.accounts.forEach((acc) => {
        if (acc !== account) {
          setNullIfPrintedCheck(acc);
        }
      });
    }

    this.setValidAccountTypes();
  }

  clearAccount(account: EmployeePaymentAccountVm) {
    account.accountType = null;
    account.accountBankName = null;
    account.accountName = null;
    account.accountNumber = null;
    account.accountRn = null;
    account.bankNameVm = null;
    account.value = 0;

    this.vm.recalc();
  }

  isPrintedCheck(account: EmployeePaymentAccountVm): boolean {
    return account.accountType === PaymentAccountType.PrintedCheck;
  }

  /** True if this is the remaining amount account */
  isRemainingAmount(index: number): boolean {
    if (this.vm.isPercent) {
      return false; // when percent split, no account is the remaining amount account
    }
    switch (this.vm.accountCount ) {
      case ActiveAccounts.OneAccount:    return index === 0;
      case ActiveAccounts.TwoAccounts:   return index === 1;
      case ActiveAccounts.ThreeAccounts: return index === 2;
      default: return false;
    }
  }

  private getBankName(account: EmployeePaymentAccountVm) {
    const accountType = account.accountType;
    const accountRn = (account.accountRn || '').trim();
    account.accountBankName = account.bankNameVm = '';
    if (accountType && accountType !== PaymentAccountType.PrintedCheck && accountRn) {
      this.bankRnService.getBankName(account.accountRn).subscribe((bn: string) => {
        if (bn) {
          account.accountBankName = account.bankNameVm = bn;
        } else {
          account.bankNameVm = `No bank for ${account.accountRn}`;
        }
        this.modelComponent.notifyOfModelChange(); // re-validates and updates the view
      });
    }
  }

  isLast(ix: number): boolean {
    return ix === this.vm.lastAccountIx;
  }

  routingNumberExit(account: EmployeePaymentAccountVm, exitEvent: ExitEvent) {
    if (exitEvent.isChanged && exitEvent.exitType !== 'destroyed') {
      this.getBankName(account);
    }
  }

  save(): Observable<EmployeePaymentMethod> {
    return this.viewService.save(this.vm);
  }

  setPaymentMethod() {
    switch (this.vm.paymentMethod) {
      case PaymentMethod.PrintedCheck:
        this.vm.accountCount = ActiveAccounts.OneAccount;
        this.accounts[0].accountType = PaymentAccountType.PrintedCheck;
        break;

      case PaymentMethod.DepositOrPaycard:
        if (!this.vm.accountCount) {
          this.vm.accountCount = ActiveAccounts.OneAccount;
        }

        // Ensure no account type is a printed check nor can become a printed check
        this.accounts.forEach(setNullIfPrintedCheck);
        this.accountTypes = this.accountTypes2;
        break;

      case PaymentMethod.Both:
        // An account type could be a printed check
        this.accountTypes = this.accountTypes3;
        // Have either two or three accounts
        if (this.vm.accountCount !== ActiveAccounts.ThreeAccounts) {
          this.vm.accountCount = ActiveAccounts.TwoAccounts;
        }
        this.vm.isPercent = false;
    }
    this.setValidAccountTypes();
  }

  /**
   * Ensure:
   * 1. In Both mode printed-check can only be the last item (legacy limitation) per PKSP-21648
   */
  setValidAccountTypes() {
    let index = 0;
    this.vm.accounts.forEach(acc => {
      const last = (this.vm.accountCount === ActiveAccounts.TwoAccounts && index === 1) ||
                   (this.vm.accountCount === ActiveAccounts.ThreeAccounts && index === 2);

      acc.accountTypes = (this.vm.paymentMethod === PaymentMethod.Both && last)
                              ? this.accountTypes3
                              : this.accountTypes2;
      index++;
    });
  }

  shouldShowAccount(index: number, account: EmployeePaymentAccountVm) {
    switch (index) {
      case 0: return true;
      case 1: return this.vm.accountCount !== ActiveAccounts.OneAccount;
      case 2: return this.vm.accountCount === ActiveAccounts.ThreeAccounts;
      default: return false;
    }
  }

  get showAllocationCell(): boolean {
    return !(
      (!this.vm.isPercent && this.vm.accountCount === ActiveAccounts.OneAccount) ||
      (this.vm.accountCount === ActiveAccounts.OneAccount &&
        this.vm.paymentMethod === PaymentMethod.DepositOrPaycard) ||
        (this.vm.paymentMethod === PaymentMethod.Both)
    );
  }

  get showAmountCell(): boolean {
    return this.vm.accountCount !== ActiveAccounts.OneAccount ||
      this.vm.paymentMethod !== PaymentMethod.DepositOrPaycard;  }

  splitValueChanged(account: EmployeePaymentAccountVm) {
    this.vm.recalc();
  }

  valueChanged(account: EmployeePaymentAccountVm) {
    if (this.vm.isPercent && this.vm.accountCount !== ActiveAccounts.OneAccount) {
      this.vm.recalc();
    }
  }
}

function setNullIfPrintedCheck(account: EmployeePaymentAccountVm) {
  if (account.accountType === PaymentAccountType.PrintedCheck) {
    account.accountType = null;
  }
}
