import { Component, OnInit } from '@angular/core';
import { ngIfAnim } from '@animations';

import { Observable } from 'rxjs';

import { SaverFactoryService } from '@core';
import { WellKnownRoutes } from '@core/well-known-routes';

import { EmployeeViewService } from '@employees/employee/employee-view.service';
import { IEmployeeTaxes, TaxBehaviorWrapper } from '@app/core/services/tax-services/tax-models';
import { Employee, EmployeeTax, TaxTypeCode } from '@model';

@Component({
  selector: 'sp-ee-tax',
  templateUrl: './employee-taxes.component.html',
  animations: [ngIfAnim],
  styleUrls: ['../employee-tab.scss']
})
export class EmployeeTaxesComponent implements OnInit {
  employee: Employee;
  eeTaxes: IEmployeeTaxes;
  WellKnownRoutes = WellKnownRoutes;
  showOtherTaxes = false;

  constructor(
    private employeeViewService: EmployeeViewService,
    private saverFactory: SaverFactoryService
  ) { }

  ngOnInit() {
    this.employeeViewService.currentEmployeeWithTaxesSnapshot$.subscribe(snapshot => {
      this.employee = snapshot.ee;
      this.eeTaxes = snapshot.eeTaxes;
    });
  }

  canLeaveAfterSave(): Observable<boolean> {
    return this.save();
  }

  isWorkAddressSet(): boolean {
    return this.eeTaxes.employeeLocation.workAddressId != null && this.eeTaxes.employeeLocation.workAddressId !== '';
  }

  hasFederalTaxes(requireInput: boolean): boolean {
    return this.eeTaxes.taxes.some(t =>
      t.taxTypeCode === TaxTypeCode.Federal &&
      t.behavior.shouldShow &&
      t.behavior.requireUserInput === requireInput
    );
  }

  hasStateTaxes(requireInput: boolean): boolean {
    return this.eeTaxes.taxes.some(t =>
      t.taxTypeCode === TaxTypeCode.State &&
      t.behavior.shouldShow &&
      t.behavior.requireUserInput === requireInput
    );
  }

  hasLocalTaxes(requireInput: boolean): boolean {
    return this.eeTaxes.taxes.some(t =>
      t.taxTypeCode !== TaxTypeCode.Federal &&
      t.taxTypeCode !== TaxTypeCode.State &&
      t.behavior.shouldShow &&
      t.behavior.requireUserInput === requireInput
    );
  }

  getFederalTaxes(requireInput: boolean): TaxBehaviorWrapper<EmployeeTax>[] {
    return this.eeTaxes.taxes.filter(t =>
      t.taxTypeCode === TaxTypeCode.Federal &&
      t.behavior.shouldShow &&
      t.behavior.requireUserInput === requireInput
    );
  }

  getStateTaxes(requireInput: boolean): TaxBehaviorWrapper<EmployeeTax>[] {
    return this.eeTaxes.taxes.filter(t =>
      t.taxTypeCode === TaxTypeCode.State &&
      t.behavior.shouldShow &&
      t.behavior.requireUserInput === requireInput
    );
  }

  getLocalTaxes(requireInput: boolean): TaxBehaviorWrapper<EmployeeTax>[] {
    return this.eeTaxes.taxes.filter(t =>
      t.taxTypeCode !== TaxTypeCode.Federal &&
      t.taxTypeCode !== TaxTypeCode.State &&
      t.behavior.shouldShow &&
      t.behavior.requireUserInput === requireInput
    );
  }

  hasOtherTaxes(): boolean {
    if (this.eeTaxes == null || this.eeTaxes.taxes.length === 0) {
      return false;
    }

    return this.eeTaxes.taxes.some(e => e.behavior.shouldShow && e.behavior.requireUserInput === false);
  }

  private save() {
    return this.saverFactory.create()
      .add(this.employeeViewService.saveEmployee(this.employee))
      .add(this.employeeViewService.saveEmployeeTaxes(this.eeTaxes))
      .go();
  }
}
