import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';

import { first } from 'rxjs/operators';

import { SaverFactoryService, SubSink, WellKnownRoutes } from '@core';

import { EmployeeViewService } from '@employees/employee/employee-view.service';
import { AddressModalComponent } from '@shared';
import { Address, Employee, EmployeeStatus, EmployeeType } from '@model';

@Component({
  selector: 'sp-ee-employment',
  templateUrl: './employee-employment.component.html',
  styleUrls: ['../employee-tab.scss']
})
export class EmployeeEmploymentComponent implements OnInit, OnDestroy {
  addWorkAddressLink = WellKnownRoutes.CompanyInfo;
  EmployeeStatus = EmployeeStatus;
  EmployeeType = EmployeeType;
  isHousehold: boolean;
  /** Company's main location */
  mainWorkAddress: Address;
  /** Sorted work addresses */
  workAddresses: Address[];
  private subs = new SubSink();
  vm: Employee;

  @ViewChild(AddressModalComponent) addressModal: AddressModalComponent;

  constructor(
    private employeeViewService: EmployeeViewService,
    private saverFactory: SaverFactoryService
  ) { }

  ngOnInit() {
    this.subs.sink = this.employeeViewService.workAddresses$.subscribe(
      addresses => {
        this.mainWorkAddress = addresses.find(addr => addr.isMain);
        this.workAddresses = addresses;
      }
    );

    this.employeeViewService.currentEmployeeSnapshot$.subscribe(ee => {
      this.vm = ee;
      const workAddressId = ee.workAddressId;
      if (!this.mainWorkAddress) {
        // no main Company address (which should be an error)? Clear EE work address
        ee.workAddressId = null;
      } else if (!ee.workAddressId) {
        // no EE work address, initialize to the main work address
        ee.workAddressId = this.mainWorkAddress.id;
      } else if (!this.workAddresses.find(addr => addr.id === ee.workAddressId)) {
        // old location no longer valid; reset to main location
        ee.workAddressId = this.mainWorkAddress.id;
      }
    }
    );

    this.employeeViewService.currentCompany$.pipe(first()).subscribe(
      company => this.isHousehold = company.isHousehold
    );
  }

  addWorkAddress() {
    this.addressModal.addOrEditAddress({ isHome: false } as Address).subscribe(
      addr => {
        if (addr) {
          this.vm.workAddressId = addr.id;
        }
      }
    );
  }

  canLeaveAfterSave() {
    return this.save();
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  private save() {
    return this.saverFactory.create()
      .add(this.employeeViewService.saveEmployee(this.vm))
      .go();
  }
}
