import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Company } from '@model';

@Component({
  selector: 'sp-ee-setup-questions',
  template: `
  <sp-model [model]="company" modelType="Company" group="employeeSetup">
    <sp-radio-group name="hasPriorPayroll" propertyName="hasPriorPayroll"
      (change)="hasPriorPayrollChanged()"
      label="Have you paid any employees this year?">
      <sp-radio-button id="priorPayrollNo" [value]="false">No</sp-radio-button>
      <sp-radio-button id="priorPayrollYes" [value]="true">Yes</sp-radio-button>
    </sp-radio-group>

    <sp-radio-group name="needDeductionSetup" propertyName="needDeductionSetup"
      (change)="needDeductionSetupChanged()"
      label="Do some employees have payroll deductions such as medical insurance, retirement plans, or garnishments?">
      <sp-radio-button id="deductionsNo" [value]="false">No</sp-radio-button>
      <sp-radio-button id="deductionsYes" [value]="true">Yes</sp-radio-button>
    </sp-radio-group>
  </sp-model>
  `
})
export class EmployeeSetupQuestionsComponent {
  @Input() company: Company;
  @Output() companyChanged = new EventEmitter();

  hasPriorPayrollChanged() {
    this.companyChanged.emit('hasPriorPayrollChanged');
  }

  needDeductionSetupChanged() {
    this.companyChanged.emit('needDeductionSetupChanged');
  }
}
