import { Injectable } from '@angular/core';

import { combineLatest, defer, Observable, of } from 'rxjs';
import { distinctUntilChanged, map } from 'rxjs/operators';

import { EntitySelectors } from '@entity';
import { environment } from '@environment';
import { SessionService } from '@app/store/session';

@Injectable({ providedIn: 'root' })
export class DeductionsEnabledService {
  /** Feature flag. Delete after deductions go live */
  featureEnabled = !!environment.enableDeductions;

  /** Live observable emits true when should show Deductions at Company and EE setup levels */
  showDeductions$: Observable<boolean>;

  /** Live observable emits true when current company has active Company deductions */
  hasCompanyDeductions$: Observable<boolean>;

  constructor(entitySelectors: EntitySelectors, sessionService: SessionService) {
    this.hasCompanyDeductions$ = entitySelectors.companyDeduction.entities$.pipe(
      map(deductions => deductions.filter(d => !d.isRemoved).length > 0)
    );

    // defer because might toggle this.enabled for testing
    // TODO: remove dependency on featureEnabled when Deductions go live
    this.showDeductions$ = defer(() => {
      return this.featureEnabled
        ? combineLatest(
            this.hasCompanyDeductions$,
            sessionService.currentCompany$.pipe(
              map(c => !!c.needDeductionSetup),
              distinctUntilChanged()
            )
          ).pipe(
            map(
              ([hasCompanyDeductions, needDeductionSetup]) => hasCompanyDeductions || needDeductionSetup
            )
          )
        : of(false);
    });
  }
}
