import { Injectable } from '@angular/core';

import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { SessionService } from '@session';

@Injectable({ providedIn: 'root' })
export class EarningsEnabledService {
  /** Live observable emits true when should show Earnings UX */
  showEarnings$: Observable<boolean>;

  constructor(sessionService: SessionService) {
    this.showEarnings$ = sessionService.currentCompany$.pipe(
      map(company => company.showEarningsTab)
    );
  }
}
