import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';

import { combineLatest, BehaviorSubject, Observable } from 'rxjs';
import { filter, finalize, first, map, startWith, takeWhile, tap } from 'rxjs/operators';

import { EmployeeListViewService } from './employee-list-view.service';
import { EmployeeListVm, EmployeeVm } from './employee-list-vm';
import { ngIfAnim } from '@animations';
import { SaverFactoryService, WellKnownRoutes } from '@core';
import { SessionService } from '@session/session.service';

@Component({
  selector: 'sp-ee-list-container',
  templateUrl: './employee-list-container.component.html',
  animations: [ngIfAnim],
})
export class EmployeeListContainerComponent implements OnInit, OnDestroy {

  employeeVms$: Observable<EmployeeVm[]>;
  hasActiveEes: boolean;
  private vm: EmployeeListVm;
  vm$: Observable<EmployeeListVm>;

  private inflightSaveCounter = 0;
  private inflightSaveCounterSubject = new BehaviorSubject<number>(0);

  back: string = WellKnownRoutes.CompanyInfo;
  next: string = WellKnownRoutes.CompanyTaxInfo;

  constructor(
    private employeeListViewService: EmployeeListViewService,
    private router: Router,
    private saverFactory: SaverFactoryService
  ) {}

  ngOnInit() {
    window.scrollTo(0, 0);

    this.vm$ = this.employeeListViewService.vm$.pipe(
      tap(vm => this.vm = vm) // need VM in this class
    );

    this.employeeVms$ = this.employeeListViewService.employeeVms$.pipe(
      map(ees => {
        const activeEes = ees.filter(ee => !ee.isRemoved);
        this.hasActiveEes = activeEes.length > 0; // template needs hasActiveEes
        return this.vm && this.vm.showRemoved ? ees : activeEes;
      })
    );
  }

  ngOnDestroy() {

  }

  canLeaveAfterSave() {
    return this.save();
  }

  AddEmployee() {
    this.employeeListViewService.createNewEmployee().subscribe(ee => {
      this.router.navigate([`${WellKnownRoutes.Employees}/${ee.id}/'personal'`]);
    });
  }

  setEmployeeIsRemoved(eeVm: EmployeeVm) {
    // Don't wait to leave the page. Start saving the removal immediately.
    // Incrementing count when start and decrementing when done.
    this.inflightSaveCounterSubject.next(this.inflightSaveCounter += 1);
    this.employeeListViewService.saveEmployeeIsRemoved(eeVm).pipe(
      finalize(() => this.inflightSaveCounterSubject.next(this.inflightSaveCounter -= 1))
    ).subscribe();
  }

  private save() {
    // When countdown to zero, no more pending removals
    const inflightSaveCountDown$ = this.inflightSaveCounterSubject.pipe(
      startWith(1), // must emit at least once
      takeWhile(v => v > 0)
    );

    return this.saverFactory.create()
      .add(
        this.employeeListViewService.saveCompany(this.vm.company),
        inflightSaveCountDown$ // wait until no more pending removals
      )
      .go();
  }

  /** Save company change immediately. Typically called when an answer to a setup question changes. */
  saveCompanyImmediately() {
    // Don't wait to leave the page. Start saving the company immediately.
    // Incrementing count when start and decrementing when done.
    this.inflightSaveCounterSubject.next(this.inflightSaveCounter += 1);
    this.employeeListViewService.saveCompany(this.vm.company).pipe(
      finalize(() => this.inflightSaveCounterSubject.next(this.inflightSaveCounter -= 1))
    ).subscribe();
  }
}
