import { Injectable } from '@angular/core';

import { Observable, of, throwError} from 'rxjs';
import { concatMap, map, take, withLatestFrom } from 'rxjs/operators';

import { areDifferent } from '@core';
import { EmployeeService, EmployeePaymentMethodService} from '@entity';
import { EmployeePaymentMethod } from '@model';
import { EmployeePaymentMethodVm } from '../employee-payment-method-vm';

@Injectable({ providedIn: 'root' })
export class EmployeePaymentAccountViewService {

  constructor(
    private employeePaymentMethodService: EmployeePaymentMethodService,
    private employeeService: EmployeeService
  ) { }

  /**  Return terminating observable of payment record for the employee with the given id */
  private getEmployeePaymentMethodByEeId$(eeId: string) {
    return this.employeeService.entityMap$.pipe(
      map(eeMap => {
        const ee = eeMap[eeId];
        if (!ee) {
          throw new Error(`Employee ${eeId} not found in cache`);
        }
        return ee;
      }),
      withLatestFrom(
        this.employeePaymentMethodService.entities$
      ),
      map(([ee, payments]) => {
        payments = payments.filter(p => p.employeeId === ee.id);
        // there should be exactly one payments for the given employee
        if (payments.length > 1) {
          throw new Error(`Employee ${ee.id} has more than one EmployeePaymentMethod`);
        }
        if (payments.length === 0) {
          throw new Error(`Employee ${ee.id} has no EmployeePaymentMethod`);
        }
        const payment = payments[0];
        return { ee, payment };
      }),
      take(1),
    );
  }

  /**  Return terminating observable of ViewModel for the EE payment method record of the employee with the given id */
  getEmployeePaymentMethodVm$(eeId: string): Observable<EmployeePaymentMethodVm> {
    return this.getEmployeePaymentMethodByEeId$(eeId).pipe(
      map(({ ee, payment }) => ee && payment ? new EmployeePaymentMethodVm(ee, payment) : null )
    );
  }

  /**
   * Save the EmployeePaymentMethod for the given EmployeePaymentMethod ViewModel
   * @returns Observable of save operation emitting the saved EmployeePaymentMethod
   */
  save(vm: EmployeePaymentMethodVm): Observable<EmployeePaymentMethod> {
    if (!vm) {
      return of(null);
    } else if (!vm.employeeId) {
      return throwError('EmployeePaymentMethod must have an Employee id to save it');
    } else {
      return this.getEmployeePaymentMethodByEeId$(vm.employeeId)
        .pipe(
          concatMap(({payment: oldPayment}) => {
            const newPayment = vm.toEmployeePaymentMethod();
            if (areDifferent(newPayment, oldPayment)) {
              if (oldPayment && oldPayment.id) {
                newPayment.id = oldPayment.id;
              }
              return this.employeePaymentMethodService.upsert(newPayment);
            } else {
              return of(oldPayment);
            }
          })
        );
    }
  }
}
