import { Component, OnInit, ViewChild } from '@angular/core';

import { Observable } from 'rxjs';

import { Employee } from '@model';
import { EmployeeViewService } from '@employees/employee/employee-view.service';
import { EmployeePaymentAccountVm } from '../employee-payment-account-vm';
import { EmployeePaymentAccountsComponent } from '../employee-payment-accounts/employee-payment-accounts.component';
import { ngIfAnim } from '@animations';
import { SaverFactoryService, LoggerService } from '@core';


@Component({
  selector: 'sp-ee-payment',
  templateUrl: './employee-payment.component.html',
  styleUrls: ['../../employee-tab.scss'],
  animations: [ngIfAnim]
})
export class EmployeePaymentComponent implements OnInit {
  @ViewChild('paymentAccounts') paymentAccountsComponent: EmployeePaymentAccountsComponent;
  vm: Employee;

  constructor(
    private employeeViewService: EmployeeViewService,
    private saverFactory: SaverFactoryService,

  ) {
  }

  ngOnInit() {
    this.employeeViewService.currentEmployeeSnapshot$.subscribe(ee => this.vm = ee);
  }

  canLeaveAfterSave(): Observable<boolean> {
    return this.save();
  }

  payRateChanged() {
    if (this.vm.payType === 'hourly' && this.vm.payRate) {
      this.vm.overtimeRate = this.vm.payRate * 1.5; // Initialize to 1.5 * payrate
    }
  }
  getMaskType() {
    switch (this.vm.payType) {
      case 'hourly':
        return 'currency_per_hour';
      case 'salary':
        return 'currency_per_year';
      case 'unit':
        return 'currency_per_unit';
      case 'mile':
        return 'currency_per_mile';
      default:
        return 'currency_per_year';
    }
  }

  private save() {
    return this.saverFactory.create()
      .add(this.employeeViewService.saveEmployee(this.vm))
      // PaymentAccountsComponent exists unless terminated
      .add(this.paymentAccountsComponent ? this.paymentAccountsComponent.save() : null)
      .go();
  }

}
