import { Component, OnInit, ViewChild } from '@angular/core';
import { ngIfAnim } from '@animations';

import { Observable, of } from 'rxjs';
import { finalize } from 'rxjs/operators';

import { SaverFactoryService } from '@core';

import { EmployeeWithAddresses, EmployeeViewService } from '@employees/employee/employee-view.service';
import { SexCodes } from '@model/codes-enums';
import { AddressModalComponent } from '@app/shared';
import { AddressFormComponent } from '@app/shared/address/address-form.component';
import { Address } from '@app/model';

@Component({
  selector: 'sp-ee-personal',
  templateUrl: './employee-personal.component.html',
  animations: [ngIfAnim],
  styleUrls: ['../employee-tab.scss']
})
export class EmployeePersonalComponent implements OnInit {
  sexes = SexCodes.codes;
  vm: EmployeeWithAddresses;
  originalAddressCodes: { geoCode: string, schoolDistrictCode?: number, psdCode: number, isVerified: boolean };
  private isModalOpen = false;
  isNew = false;

  @ViewChild(AddressModalComponent)
  addressModal: AddressModalComponent;

  @ViewChild(AddressFormComponent)
  addressForm: AddressFormComponent;

  constructor(
    private employeeViewService: EmployeeViewService,
    private saverFactory: SaverFactoryService
  ) { }

  ngOnInit() {
    this.employeeViewService.currentEmployeeWithAddressesSnapshot$.subscribe(vm => {
      this.vm = vm;
      this.originalAddressCodes = {
        geoCode: vm.home.geoCode,
        schoolDistrictCode: vm.home.schoolDistrictCode,
        psdCode: vm.home.psdCode,
        isVerified: vm.home.isVerified && !!vm.home.geoCode
      };
      this.isNew = !vm.hasName;
    });
  }

  editAddress(address: Address) {
    if (!this.isModalOpen) {
      this.isModalOpen = true;
      this.addressModal.addOrEditAddress(address)
      .pipe(finalize(() => this.isModalOpen = false))
      .subscribe(addr => {
        if (addr) {
          this.addressForm.confirmAddress(addr as any);
        }
      });
    }
  }

  canLeaveAfterSave(): boolean | Observable<boolean> {
    return this.vm ? this.save() : true; // can leave immediately if no VM
  }

  canVerify(address: Address) {
    return !this.isModalOpen && !address.isVerified &&
      !this.isModalOpen && address.street1 && address.city && address.state && address.zip;
    }

  private save() {
    const saver = this.saverFactory.create();
    if (this.vm.hasName) {
      // can save and leave if the EE has a name
      saver.add(this.employeeViewService.saveEmployeeWithHome(this.vm));
    } else if (this.isNew) {
      // No name but it is a new EE; set soft delete (isRemoved:true), save that, and then leave.
      this.vm.isRemoved = true;
      saver.add(this.employeeViewService.saveEmployeeIsRemoved(this.vm));
    } else {
      // Blocker: user cleared name of existing EE; don't leave
      saver.add(of(false));
    }
    return saver.go();
  }
}
