import { Component, ElementRef, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { EmployeeVm } from './employee-list-vm';

@Component({
  selector: 'sp-ee-list',
  templateUrl: './employee-list.component.html'
})
export class EmployeeListComponent {
  @Input() employees: EmployeeVm[];
  @Input() showRemoved = false;
  @Input() readOnly = false;
  @Output() isRemovedChanged = new EventEmitter<EmployeeVm>();
  @ViewChild('deleteModalContent') deleteModalContentEl: ElementRef;

  deleteConfirmationName: string;
  selectedEe: EmployeeVm;

  employeeTypeLookup = {
    fulltime: 'Full-Time',
    parttime: 'Part-Time',
    contractor: 'Contractor',
    household: 'Househld',
    officer: 'Officer',
    terminated: 'Terminated',
    '': ''
  };

  constructor(private modalService: NgbModal) {}

  byId(eeVm: EmployeeVm) {
    return eeVm.id;
  }

  openDeleteConfirmation(selectedEe: EmployeeVm) {
    this.deleteConfirmationName = '';
    this.selectedEe = selectedEe;
    this.openDeleteModal();
  }

  get isDeleteConfirmed() {
    return this.selectedEe.name === this.deleteConfirmationName;
  }

  private async openDeleteModal() {
    try {
      const result = await this.modalService.open(this.deleteModalContentEl).result;
      if (result === 'delete' && this.isDeleteConfirmed) {
        this.removeEmployee(this.selectedEe);
      }
    } catch (error) {
      // do nothing
    }
  }

  removeEmployee(eeVm: EmployeeVm) {
    eeVm.isRemoved = true;
    this.isRemovedChanged.emit(eeVm);
  }

  toggleIsRemoved(eeVm: EmployeeVm) {
    if (eeVm.isRemoved) {
      eeVm.isRemoved = false;
      this.isRemovedChanged.emit(eeVm);
    } else {
      this.openDeleteConfirmation(eeVm);
    }
  }

  toLink(eeVm: EmployeeVm) {
    return eeVm.isRemoved ? undefined : eeVm.id;
  }
}
