import { Component, OnInit } from '@angular/core';

import { combineLatest, Observable } from 'rxjs';
import { map, first } from 'rxjs/operators';

import { CompanyEarningService, CompanyService, EarningService } from '@entity';
import { Earning, EarningsCategory } from '@model';

/** ViewModel for display of an individual Earning */
export class EarningVm {
  code: string;
  name: string;
  description: string;
  typeName: string;
}

export interface EarningsListVm {
  displayEarnings$: Observable<boolean>;
  defaultEarnings$: Observable<EarningVm[]>;
  additionalEarnings$: Observable<EarningVm[]>;
}

@Component({
  selector: 'sp-payment-earnings-list',
  templateUrl: './employee-payment-earnings-list.component.html',
})

export class EmployeePaymentEarningsListComponent implements OnInit {

  vm: EarningsListVm;

  constructor(
    private companyEarningsService: CompanyEarningService,
    private companyService: CompanyService,
    private earningService: EarningService,
  ) { }

  ngOnInit() {
    const displayEarnings$ = this.companyService.currentCompany$.pipe(map(c => c.hasPriorPayroll));

    const defaultEarnings$ = this.earningService.earnings$
      .pipe(
        first(),
        map(defaultEarning => {
          return defaultEarning
            .filter(q => q.earningsCategory.toUpperCase() === EarningsCategory.DEFAULTOTHERCOMPENSATION)
            .map(earning => {
              return {
                code: earning.code,
                name: earning.name,
                description: earning.description,
                typeName: earning.earningTypeName
              };
            });
        })
      );

    const additionalEarnings$ = combineLatest(
      this.companyEarningsService.companyEarnings$,
      this.earningService.earnings$)
      .pipe(
        first(),
        map(([companyEarning, earnings]) => {
          return companyEarning
            .filter(ce => !ce.isRemoved)
            .map(ce => {
              return {
                code: ce.earningCode,
                name: ce.name,
                description: ce.description,
                typeName: (earnings.find(e => e.code === ce.earningCode) || {} as Earning).earningTypeName || ''
              };
            })
            .sort((a, b) => a.typeName.toLowerCase().localeCompare(b.typeName.toLowerCase()));
        })
      );

    this.vm = { displayEarnings$, defaultEarnings$, additionalEarnings$ };
  }
}
