import { Injectable } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';

import { combineLatest, Observable, of, throwError } from 'rxjs';
import { concatMap, debounceTime, distinctUntilChanged, filter, map, scan, shareReplay, take } from 'rxjs/operators';

import { areDifferent, Saver, WellKnownRoutes } from '@core';
import { Address, Employee, Company, EmployeeType, EmployeePaymentMethod } from '@model';
import { AddressService, EmployeeService } from '@entity';
import { environment } from '@environment';
import { IEmployeeTaxes } from '@core/services/tax-services/tax-models';
import { SessionService } from '@session/session.service';
import { TaxCollectorService } from '@core/services/tax-services/tax-collector.service';

const employeesFeatureUrl = WellKnownRoutes.Employees;

/** ViewModel for the current Employee and its Home and Work address */
export interface EmployeeWithAddresses extends Employee {
  home?: Address;
  work?: Address;
}

export interface EmployeeWithTaxes {
  ee: Employee;
  eeTaxes: IEmployeeTaxes;
}

/**
 * Share an Employee view model and persistence services among a set
 * of closely cooperating Employee components
 * TODO: consider delegating work to sub-services targeting particular components.
 */
@Injectable({providedIn: 'root'})
export class EmployeeViewService {

  /** Observable of the current company */
  currentCompany$: Observable<Company>;

  /** Observable of the current Employee Id */
  currentEmployeeId$: Observable<string>;

  /**
   * Observable of the current non-null Employee.
   * Emits when current non-null Employee exists and when it changes in any way.
   */
  currentEmployee$: Observable<Employee>;

  /**
   * Terminating observable of the next (non-null) current Employee VM.
   * No need to unsubscribe.
   */
  currentEmployeeSnapshot$: Observable<Employee>;

  /**
   * Terminating observable of the next (non-null) current Employee VM with its addresses.
   * No need to unsubscribe.
  */
  currentEmployeeWithAddressesSnapshot$: Observable<EmployeeWithAddresses>;

  /** T
   * erminating observable of the next (non-null) current Employee VM with its taxes (if any).
   * No need to unsubscribe.
   */
  currentEmployeeWithTaxesSnapshot$: Observable<EmployeeWithTaxes>;

  /** Observable of work addresses for the current company */
  workAddresses$: Observable<Address[]>;

  /** Boolean observable of whether current user is an admin */
  isAdmin$: Observable<boolean>;

  /** Boolean observable of whether can save (false) or not save (true) changes to the current company */
  readOnly$: Observable<boolean>;

  constructor(
    private addressService: AddressService,
    private employeeService: EmployeeService,
    private router: Router,
    private sessionService: SessionService,
    private taxCollectorService: TaxCollectorService
  ) {
    this.currentCompany$ = this.sessionService.currentCompany$;
    this.currentEmployeeId$ = this.sessionService.currentEmployeeId$;

    /**
     * Observable of a clone of the current Employee when current Employee exists and changes in any way
     * Emits null when no current Employee.
     * Do NOT expose to consumers.
     * They should only see non-null Employee.
     */
    const currentEmployee$ = combineLatest(
      this.currentEmployeeId$,
      this.sessionService.currentEmployee$,
      this.sessionService.companyPackageLoaded$,
      this.sessionService.isSignedIn$,
      ).pipe(
      // debounce synchronous chatter from input observables by waiting one tick
      debounceTime(0),

      // Map to a clone of the current Employee if it exists
      // else map to null (which is not emitted)
      map(([eeId, employee, isLoaded, isSignedIn]) => {

        // Can arrive here with no eeId because some other service cleared it
        // or with an existing EE but no company package (because returning to admin console)
        // or while signing-out.
        // Do NOT emit an Employee. Continue instead without error.
        if (!eeId || !isLoaded || !isSignedIn) {
          return null;
        }

        if (employee) {
          return Employee.create(employee); // cloned Employee instance
        }

        // No valid Employee for the currentEmployeeId
        // Can happen if deep link here with bad id in browser address bar
        // Could report it as follows ... but won't for now.
        // const error = `EmployeeViewService did not find EE record for id=${eeId}`;
        // this.sessionService.logger.error(error);

        // Clear the currentEmployeeId and navigate to EE setup
        this.setCurrentEmployeeId(null);
        this.router.navigate([WellKnownRoutes.Employees], { replaceUrl: true });
        return null;
      }),

      // Expensive to calculate so hold onto last value
      shareReplay(1),
    );

    // Non-null currentEmployee$ (do not emit a null EE because EE views will fail).
    this.currentEmployee$ = currentEmployee$.pipe(
      filter(ee => ee != null)
    );

    this.currentEmployeeSnapshot$ = this.currentEmployee$.pipe(take(1));

    this.currentEmployeeWithAddressesSnapshot$ = combineLatest(
      this.currentEmployee$,
      this.addressService.entityMap$
    ).pipe(
      take(1),
      map(([ee, addressMap]) => {
        const home = addressMap[ee.homeAddressId];
        if (!home) {
          // No Home Address record for this id. Shouldn't happen (but it did during db migration issue #93!)
          const error = `EmployeeViewService did not find home address (${ee.homeAddressId}) for EE id=${ee.id}`;
          throw new Error(error);
        }

        const work = addressMap[ee.workAddressId];

        const vm: EmployeeWithAddresses = ee;
        vm.home = Address.create(home);
        vm.work = work == null ? null : Address.create(work);
        return vm;
      })
    );

    this.currentEmployeeWithTaxesSnapshot$ = combineLatest(
      this.currentEmployee$,
      this.taxCollectorService.taxes$
    ).pipe(
      take(1),
      map(([ee, taxes]) => {
        const eeTaxes = taxes
          ? taxes.employeeTaxes.find(e => e.employeeLocation.id === ee.id)
          : null;
        return { ee, eeTaxes };
      })
    );

    // Clear current employee if leaving Employees Feature or going to EE Setup to pick an EE
    // Service never dies so no need to unsubscribe
    router.events.pipe(
      filter(event => event instanceof NavigationEnd),
      map((event: NavigationEnd) => {
        const url = event.urlAfterRedirects;
        return !url.startsWith(employeesFeatureUrl) || url === employeesFeatureUrl;
      }),
      distinctUntilChanged()
    ).subscribe(shouldClear => {
      if (shouldClear) {
        this.setCurrentEmployeeId(null);
      }
    });

    this.workAddresses$ = addressService.workAddresses$;
  }

  /** Save existing EE vm (or part of one) by merging it with the EE in cache */
  saveEmployee(eeVm: Partial<EmployeeWithAddresses>): Observable<Employee> {
    if (eeVm == null) {
      return of(null); // nothing to save
    }

    if (!eeVm.id) {
      return throwError('saveEmployee: no Employee or lacks its id');
    }

    // Trick: extract pure employee data from the eeVm,
    // by assigning the addresses into (unused) vars
    const { home, work, ...ee } = eeVm;

    return this.employeeService.entityMap$.pipe(
      take(1),
      map(eeMap => {
        const oldEe: Employee = eeMap[ee.id];
        // strip off the prototype before comparing
        const newEe: Employee = Object.assign({}, oldEe, ee);

        const changed = areDifferent(newEe, oldEe, 'EE');
        const computeTaxes = oldEe.workAddressId !== newEe.workAddressId ||
          ((oldEe.employeeType === EmployeeType.Contractor || newEe.employeeType === EmployeeType.Contractor) &&
            oldEe.employeeType !== newEe.employeeType);

        return {
          eeMap: eeMap,
          changed: changed,
          computeTaxes: computeTaxes,
          newEe: newEe,
          oldEe: oldEe
        };
      }),
      concatMap(context => {
        return context.changed
          ? this.employeeService.update(context.newEe, { isOptimistic: true })
          : of(context.oldEe);
      }, (context, updateResult) => ({ ...context, updateResult })),
      concatMap(context => {
        return context.computeTaxes ? this.taxCollectorService.computeTaxes() : of(true);
      }, (context, _) => context),
      map(context => {
        return context.updateResult;
      })
    );
  }

  // Save using multi-entity save
  saveEmployeeWithHome(eeVm: EmployeeWithAddresses): Observable<Employee> {
    return this.employeeService.saveEmployeeUpdate(eeVm, eeVm.home);
  }

  saveEmployeeIsRemoved(eeVm: { id: string, isRemoved: boolean }): Observable<Employee> {
    return this.employeeService.saveEmployeeIsRemovedById(eeVm.id, eeVm.isRemoved);
  }

  /** Save the configured taxes array */
  saveEmployeeTaxes(employeeTaxes: IEmployeeTaxes): Observable<boolean> {
    if (!employeeTaxes) {
      return Saver.NO_SAVE; // no vm to save
    }

    return this.taxCollectorService.saveEmployeeTaxes(employeeTaxes);
  }

  /** Set new current EmployeeId */
  setCurrentEmployeeId(eeId: string): void {
    this.sessionService.setCurrentEmployeeId(eeId);
  }
}
